    '  (b). Fraude envolvendo terceiros que poderiam ter efeito material nas demonstraes contbeis;\n' +
    '  (c). Violao ou possveis violaes de lei, normas ou regulamentos cujos efeitos deveriam ser considerados para divulgao nas demonstraes contbeis, ou mesmo dar origem ao registro de proviso para contingncias passivas.';

