import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  Box,
  DATES,
  ERROR,
  InputGroupInline,
  InputTooltipProps,
  InputType,
  showToast,
  WindowComponent,
  WindowComponentProps,
  withTooltip,
} from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { LivroFolha } from '../../../../../models/dtos/mcg/livroFolha';
import { CONSOLIDAR_ENUM_VALUES, NORMAL } from '../../../../../models/enumerated/mcg/consolidarEnum';
import {
  DIARIO_TIPO_ENUM_VALUES,
  TIPO_DIARIO_NORMAL,
  TIPO_DIARIO_SIMPLIFICADO_ALTERNANDO_CONTA,
  TIPO_DIARIO_SIMPLIFICADO_ALTERNANDO_VALOR,
} from '../../../../../models/enumerated/mcg/diarioTipoEnum';
import {
  ANALITICO_NUMBER,
  GRAU_CONTA_NUMBER,
  SINTETICO_ANALITICO_TIPO_ENUM_VALUES,
} from '../../../../../models/enumerated/mcg/sinteticoAnaliticoTipoEnum';
import { buscarConfigLivroFolhaMCG } from '../../../../../services/mcg.service';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ResponsavelIS from '../../../../components/ger/responsavelIS/ResponsavelIS';
import CustomizacaoTermoIS from '../../../../components/mcg/customizacaoTermoIS/CustomizacaoTermoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, DiarioRV, REPORT_EVENT, REPORT_TITLE } from './diarioRV';

const DEFAULT_VALUES: DiarioRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  consolidacao: NORMAL.value,
  exibirFormatacaoValor: true,
  grauContaBalancoPatrimonial: 18,
  isImprimirTituloConta: true,
  isImprimirTotalDia: true,
  tipoBalancoPatrimonial: ANALITICO_NUMBER.value,
  tipoDiario: TIPO_DIARIO_NORMAL.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  exibirConfiguracaoDadosEmpresa: false,
  exibirConfiguracaoDataAtual: true,
  exibirImprimirValoresColoridos: true,
  exibirSegmentacaoFolha: true,
};

const DiarioReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<DiarioRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [isEnabledIsMatriz, setIsEnabledIsMatriz] = useState(false);
  const [isEnabledGrau, setIsEnabledGrau] = useState(false);
  const [isEnabledDemonstrativoLucroPrejuizo, setIsEnabledDemonstrativoLucroPrejuizo] = useState(true);
  const [isEnabledPlanoConta, setIsEnabledPlanoConta] = useState(true);
  const [isEnabledBalancoAbertura, setIsEnabledBalancoAbertura] = useState(true);
  const [isEnabledBalancete, setIsEnabledBalancete] = useState(true);
  const [isEnabledDemonstracaoResultado, setIsEnabledDemonstracaoResultado] = useState(true);
  const [isEnabledBalancoPatrimonial, setIsEnabledBalancoPatrimonial] = useState(true);
  const [isEnabledAberturaEncerramento, setIsEnabledAberturaEncerramento] = useState(true);
  const [isEnabledConsolidado, setIsEnabledConsolidado] = useState(true);
  const [isEnabledSimplificado, setIsEnabledSimplificado] = useState(true);

  const carregarParametroEmpresa = () => {
    if (globalReducer.globalParameter.empresa.matriz === null) {
      setIsEnabledIsMatriz(true);
      formProps.getValues().matrizFilialDiario = globalReducer.globalParameter.empresa.centralizar;
    }
  };

  const adjustePag = () => {
    const {
      empresa: { id },
    } = globalReducer.globalParameter;

    buscarConfigLivroFolhaMCG(id, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: (result: LivroFolha) => {
        formProps.setValue('startPage', result.diarioFolha === 0 ? '1' : result.diarioFolha.toString(), true);
      },
    });
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        setTimeout(() => {
          adjustePag();
          carregarParametroEmpresa();
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const enabledGrau = () => {
    if (formProps.getValues().tipoBalancoPatrimonial === GRAU_CONTA_NUMBER.value) {
      setIsEnabledGrau(true);
    } else {
      setIsEnabledGrau(false);
      formProps.unregister(['grauContaBalancoPatrimonial']);
    }
  };

  const enabledSimplificado = () => {
    if (formProps.getValues().tipoDiario === TIPO_DIARIO_SIMPLIFICADO_ALTERNANDO_CONTA.value) {
      formProps.setValue('isImprimirTotalDia', false, true);
      setIsEnabledSimplificado(false);
    } else if (formProps.getValues().tipoDiario === TIPO_DIARIO_SIMPLIFICADO_ALTERNANDO_VALOR.value) {
      formProps.setValue('isImprimirTotalDia', true, true);
      setIsEnabledSimplificado(false);
    } else {
      formProps.setValue('isImprimirTotalDia', true, true);
      formProps.setValue('isImprimirTituloConta', true, true);
      formProps.setValue('isImprimirCodReduzido', false, true);
      setIsEnabledSimplificado(true);
    }
  };

  const enableDemonstracaoLucroPrejuizo = () => {
    if (formProps.getValues().isImprimirDemonstrLucroPrejuizo) {
      setIsEnabledDemonstrativoLucroPrejuizo(false);
    } else {
      setIsEnabledDemonstrativoLucroPrejuizo(true);
    }
  };

  const enablePlanoContas = () => {
    if (formProps.getValues().isImprimirPlanoConta) {
      setIsEnabledPlanoConta(false);
    } else {
      formProps.setValue('isImprimirSaldoPlanoConta', false, true);
      setIsEnabledPlanoConta(true);
    }
  };

  const enableConsolidado = () => {
    if (formProps.getValues().matrizFilialDiario) {
      setIsEnabledConsolidado(false);
    } else {
      setIsEnabledConsolidado(true);
    }
  };

  const enableBalancoAbertura = () => {
    if (formProps.getValues().isImprimirBalancoAbertura) {
      setIsEnabledBalancoAbertura(false);
    } else {
      setIsEnabledBalancoAbertura(true);
    }
  };

  const enableBalancete = () => {
    if (formProps.getValues().isImprimirBalancete) {
      setIsEnabledBalancete(false);
    } else {
      setIsEnabledBalancete(true);
    }
  };

  const enableDemonstracaoResultado = () => {
    if (formProps.getValues().isImprimirDemonstrativoResultado) {
      setIsEnabledDemonstracaoResultado(false);
    } else {
      setIsEnabledDemonstracaoResultado(true);
      formProps.setValue('imprimirDataInicialFinalDRE', false, true);
      formProps.setValue('isCompararPeriodoAnteriorDre', false, true);
      formProps.setValue('dtAssinaturaDre', null, true);
      formProps.setValue('outrosResponsaveisDer', null, true);
      formProps.setValue('representanteDemonstracaoResultado', null, true);
      formProps.unregister(['termoDemonstrativoResultado']);
    }
  };

  const enableBalancoPatrimonial = () => {
    if (formProps.getValues().isImprimirBalancoPatrimonial) {
      setIsEnabledBalancoPatrimonial(false);
    } else {
      setIsEnabledBalancoPatrimonial(true);
      formProps.setValue('tipoBalancoPatrimonial', ANALITICO_NUMBER.value, true);
      formProps.setValue('isSepararPaginaAtivoPassivo', false, true);
      formProps.setValue('isCompararPeriodoAnteriorBap', false, true);
      formProps.setValue('imprimirDataInicialFinalPatrimonio', false, true);
      formProps.setValue('dtAssinatura', null, true);
      formProps.setValue('outrosResponsaveisBalanco', null, true);
      formProps.setValue('representanteBalancoPatrimonial', null, true);
      enabledGrau();
      formProps.unregister(['termoBalancoPatrimonial']);
    }
  };

  const enableAberturaEncerramento = () => {
    if (formProps.getValues().isImprimirAberturaEncerramento) {
      setIsEnabledAberturaEncerramento(false);
      formProps.setValue('startPage', '1', true);
    } else {
      setIsEnabledAberturaEncerramento(true);
      formProps.setValue('usarPrimeiraDataLancAssinatura', false, true);
      formProps.setValue('exibirNroPaginaTermoAE', false, true);
      formProps.setValue('ocultarData', false, true);
      formProps.setValue('dtTermoAbertura', null, true);
      formProps.setValue('dtTermoEncerramento', null, true);
      formProps.setValue('representanteAberturaEncerramento', null, true);
      formProps.unregister(['termoAberturaEncerramento']);
      adjustePag();
    }
  };

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'auto',
    };
  };

  const acumularMatrizFilial = isEnabledIsMatriz ? (
    <Col>
      <CustomInput
        type="switch"
        label="Acumular Matriz e Filiais"
        name="matrizFilialDiario"
        onChange={enableConsolidado}
        id="idmatrizFilialDiario"
        innerRef={formProps.register}
      />
      {withTooltip(propToolTip('Acumula Matriz e Filial para o dirio e os relatrios incorporados.'), 'matrizFilialDiario')}
    </Col>
  ) : null;

  const isGrauConta = isEnabledGrau ? (
    <Col>
      <InputGroupInline
        id="grauContaBalancoPatrimonial"
        label="Grau"
        isPrependLabel={true}
        placeholder="digite o grau da conta"
        formProps={{ ...formProps, validation: { max: 2147483647, required: true } }}
        type={InputType.NUMBER}
      />
    </Col>
  ) : null;

  const isTermoAberturaEncerramento = isEnabledAberturaEncerramento ? null : (
    <Row>
      <Col>
        <CustomizacaoTermoIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Termo"
          placeholder="Selecione o Termo de Abertura e Encerramento"
          searchViewProps={{ apenasAberturaEncerramento: true }}
          apenasAberturaEncerramento={true}
          id="termoAberturaEncerramento"
          isPrependLabel={true}
          inputSearchProps={{ size: 'xl' }}
          tooltipProps={{ message: 'Termo de Abertura e Encerramento.' }}
        />
      </Col>
    </Row>
  );

  const isTermoBalancoPatrimonial = isEnabledBalancoPatrimonial ? null : (
    <Row>
      <Col>
        <CustomizacaoTermoIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Termo"
          placeholder="Selecione o Termo de Balano Patrimonial"
          searchViewProps={{ apenasBalancoPatrimonial: true }}
          apenasBalancoPatrimonial={true}
          id="termoBalancoPatrimonial"
          isPrependLabel={true}
          inputSearchProps={{ size: 'xl' }}
          tooltipProps={{ message: 'Termo de Balano Patrimonial.' }}
        />
      </Col>
    </Row>
  );

  const isTermoDemonstracaoResultado = isEnabledDemonstracaoResultado ? null : (
    <Row>
      <Col>
        <CustomizacaoTermoIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Termo"
          searchViewProps={{ apenasDemonstracaoResultado: true }}
          apenasDemonstracaoResultado={true}
          placeholder="Selecione o Termo de Demonstrao Resultado"
          id="termoDemonstrativoResultado"
          isPrependLabel={true}
          inputSearchProps={{ bsSize: 'xl' }}
          tooltipProps={propToolTip('Termo Demonstrao de Resultado')}
        />
      </Col>
    </Row>
  );

  return (
    <WindowComponent
      title="Configurao do Relatrio - Dirio"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      wikiId={6331}
    >
      <Row>
        <Col>
          <InputGroupInline
            id="tipoDiario"
            label="Tipo"
            isPrependLabel={true}
            placeholder="Selecione um Tipo"
            onChange={enabledSimplificado}
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: DIARIO_TIPO_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
        <Col>
          <InputGroupInline
            id="consolidacao"
            label="consolidacao"
            isPrependLabel={true}
            tooltipProps={propToolTip('Consolidao de lanamentos de empresas a serem exibidos no relatrio.')}
            placeholder="Selecione uma consolidao"
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: CONSOLIDAR_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput type="switch" label="Totalizar Por Dia" name="isImprimirTotalDia" id="isImprimirTotalDia" innerRef={formProps.register} />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Cdigo Reduzido"
            disabled={isEnabledSimplificado}
            name="isImprimirCodReduzido"
            id="idisImprimirCodReduzido"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Data Completa no Lanamento"
            name="isImprimirData"
            id="idisImprimirData"
            innerRef={formProps.register}
          />
          {withTooltip(propToolTip('Imprime a data completa do lanamento no formato dd/mm/aaaa ao invs de apenas o dia.'), 'isImprimirData')}
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Ttulo da Conta"
            disabled={isEnabledSimplificado}
            name="isImprimirTituloConta"
            id="isImprimirTituloConta"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput type="switch" label="Armazenar Pgina" name="isArmazenarPagina" id="isArmazenarPagina" innerRef={formProps.register} />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Notas Explicativas"
            name="isImprimirNotasExplicativas"
            id="isImprimirNotasExplicativas"
            innerRef={formProps.register}
          />
        </Col>
      </Row>
      <Row className="mb-3">
        {acumularMatrizFilial}
        <Col>
          <CustomInput
            type="switch"
            label="Incluir a palavra 'Consolidado' no nome dos relatrios"
            name="isImprimirConsolidado"
            disabled={isEnabledConsolidado}
            id="isImprimirConsolidado"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Trocar referncia para data inicial/final no cabealho do Dirio"
            name="imprimirDataInicialFinalDiario"
            id="imprimirDataInicialFinalDiario"
            innerRef={formProps.register}
          />
        </Col>
      </Row>
      <Accordion>
        <AccordionItem title="Incorporar ao Dirio">
          <Box>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  label="Demonstrao de Lucro e Prejuzo"
                  name="isImprimirDemonstrLucroPrejuizo"
                  onChange={enableDemonstracaoLucroPrejuizo}
                  id="isImprimirDemonstrLucroPrejuizo"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  label="Trocar referncia para data inicial/final"
                  name="imprimirDataInicialFinalLucroPrejuizo"
                  id="imprimirDataInicialFinalLucroPrejuizo"
                  disabled={isEnabledDemonstrativoLucroPrejuizo}
                  innerRef={formProps.register}
                />
              </Col>
              <Col />
            </Row>
            <hr />
            <Row className="mb-2">
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Plano de Contas"
                  name="isImprimirPlanoConta"
                  onChange={enablePlanoContas}
                  id="isImprimirPlanoConta"
                  innerRef={formProps.register}
                />
              </Col>
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Imprimir Saldo"
                  name="isImprimirSaldoPlanoConta"
                  disabled={isEnabledPlanoConta}
                  id="isImprimirSaldoPlanoConta"
                  innerRef={formProps.register}
                />
              </Col>
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Trocar referncia para data inicial/final"
                  name="imprimirDataInicialFinalPlanoConta"
                  disabled={isEnabledPlanoConta}
                  id="imprimirDataInicialFinalPlanoConta"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <hr />
            <Row>
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Balano de Abertura"
                  name="isImprimirBalancoAbertura"
                  onChange={enableBalancoAbertura}
                  id="isImprimirBalancoAbertura"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <InputGroupInline
                  id="dtAssinaturaBlAbertura"
                  isPrependLabel={true}
                  formProps={formProps}
                  label="Data da Assinatura"
                  disabled={isEnabledBalancoAbertura}
                  placeholder="Informe a Data da Assinatura"
                  tooltipProps={propToolTip('Caso este campo esteja vazio a data de assinatura ser o primeiro dia da referncia inicial.')}
                  type={InputType.DATE}
                />
              </Col>
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Trocar referncia para data inicial/final"
                  disabled={isEnabledBalancoAbertura}
                  name="imprimirDataInicialFinalBalancoAbertura"
                  id="imprimirDataInicialFinalBalancoAbertura"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <hr className="mt-0" />
            <Row>
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Balancete"
                  onChange={enableBalancete}
                  name="isImprimirBalancete"
                  id="isImprimirBalancete"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <InputGroupInline
                  id="dtAssinaturaBalancete"
                  isPrependLabel={true}
                  formProps={formProps}
                  label="Data da Assinatura"
                  disabled={isEnabledBalancete}
                  placeholder="Informe a Data da Assinatura"
                  tooltipProps={propToolTip('Caso este campo esteja vazio a data de assinatura ser o primeiro dia da referncia inicial.')}
                  type={InputType.DATE}
                />
              </Col>
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Trocar referencia para data inicial/final"
                  disabled={isEnabledBalancete}
                  name="imprimirDataInicialFinalBalanceteNormal"
                  id="imprimirDataInicialFinalBalanceteNormal"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
          </Box>
        </AccordionItem>
        <AccordionItem title="Demonstrao do Resultado">
          <Box>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  label="Demonstrao de Resultado"
                  name="isImprimirDemonstrativoResultado"
                  onChange={enableDemonstracaoResultado}
                  id="isImprimirDemonstrativoResultado"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  label="Trocar referencia para data inicial/final"
                  name="imprimirDataInicialFinalDRE"
                  disabled={isEnabledDemonstracaoResultado}
                  id="imprimirDataInicialFinalDRE"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <Row className="mt-2">
              <Col className="mt-2">
                <CustomInput
                  type="switch"
                  label="Comparar com o Perodo Anterior"
                  name="isCompararPeriodoAnteriorDre"
                  disabled={isEnabledDemonstracaoResultado}
                  id="isCompararPeriodoAnteriorDre"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <InputGroupInline
                  id="dtAssinaturaDre"
                  isPrependLabel={true}
                  formProps={formProps}
                  label="Data da Assinatura"
                  disabled={isEnabledDemonstracaoResultado}
                  placeholder="Informe a Data da Assinatura"
                  tooltipProps={propToolTip('Caso este campo esteja vazio a data de assinatura ser o ltimo dia da referncia.')}
                  type={InputType.DATE}
                />
              </Col>
            </Row>
            {isTermoDemonstracaoResultado}
            <Row>
              <Col>
                <ResponsavelIS
                  id="representanteDemonstracaoResultado"
                  formProps={formProps}
                  label="Representante"
                  disabled={isEnabledDemonstracaoResultado}
                  placeholder="Informe o Representante"
                  isPrependLabel={true}
                  inputSearchProps={{ bsbsSize: 'xl', selectProps: { isMulti: false } }}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <ResponsavelIS
                  id="outrosResponsaveisDer"
                  formProps={formProps}
                  label="Responsveis"
                  placeholder="Informe os Responsveis Para Assinatura"
                  disabled={isEnabledDemonstracaoResultado}
                  isPrependLabel={true}
                  inputSearchProps={{ bsbsSize: 'xl', selectProps: { isMulti: true } }}
                  tooltipProps={propToolTip('Responsveis adicionais para assinatura.')}
                />
              </Col>
            </Row>
          </Box>
        </AccordionItem>
        <AccordionItem title="Balano Patrimonial">
          <Box>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  label="Balano Patrimonial"
                  name="isImprimirBalancoPatrimonial"
                  onChange={enableBalancoPatrimonial}
                  id="isImprimirBalancoPatrimonial"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  label="Separar Pginas dos Grupos Ativo e Passivo"
                  name="isSepararPaginaAtivoPassivo"
                  disabled={isEnabledBalancoPatrimonial}
                  id="isSepararPaginaAtivoPassivo"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <Row className="mb-3">
              <Col>
                <CustomInput
                  type="switch"
                  label="Comparar com o Perodo Anterior"
                  disabled={isEnabledBalancoPatrimonial}
                  name="isCompararPeriodoAnteriorBap"
                  id="isCompararPeriodoAnteriorBap"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  label="Trocar referencia para data inicial/final"
                  disabled={isEnabledBalancoPatrimonial}
                  name="imprimirDataInicialFinalPatrimonio"
                  id="imprimirDataInicialFinalPatrimonio"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="tipoBalancoPatrimonial"
                  label="Tipo"
                  isPrependLabel={true}
                  placeholder="Selecione o tipo"
                  disabled={isEnabledBalancoPatrimonial}
                  onChange={enabledGrau}
                  formProps={{ ...formProps, validation: { required: true } }}
                  inputDropDownProps={{
                    optionsProvider: SINTETICO_ANALITICO_TIPO_ENUM_VALUES,
                    selectProps: { isClearable: false },
                  }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
              {isGrauConta}
              <Col>
                <InputGroupInline
                  id="dtAssinatura"
                  isPrependLabel={true}
                  formProps={formProps}
                  disabled={isEnabledBalancoPatrimonial}
                  label="Data da Assinatura"
                  placeholder="Informe a Data da Assinatura"
                  tooltipProps={propToolTip('Caso este campo esteja vazio a data de assinatura ser o ltimo dia da referncia.')}
                  type={InputType.DATE}
                />
              </Col>
            </Row>
            {isTermoBalancoPatrimonial}
            <Row>
              <Col>
                <ResponsavelIS
                  id="representanteBalancoPatrimonial"
                  formProps={formProps}
                  label="Representante"
                  disabled={isEnabledBalancoPatrimonial}
                  placeholder="Informe o Representante"
                  isPrependLabel={true}
                  inputSearchProps={{ bsbsSize: 'xl', selectProps: { isMulti: false } }}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <ResponsavelIS
                  id="outrosResponsaveisBalanco"
                  formProps={formProps}
                  label="Responsveis"
                  disabled={isEnabledBalancoPatrimonial}
                  placeholder="Informe os Responsveis Para Assinatura"
                  isPrependLabel={true}
                  inputSearchProps={{ bsbsSize: 'xl', selectProps: { isMulti: true } }}
                  tooltipProps={propToolTip('Responsveis adicionais para assinatura.')}
                />
              </Col>
            </Row>
          </Box>
        </AccordionItem>
        <AccordionItem title="Termo de Abertura e Encerramento">
          <Box>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  label="Termo de Abertura e Encerramento"
                  name="isImprimirAberturaEncerramento"
                  onChange={enableAberturaEncerramento}
                  id="isImprimirAberturaEncerramento"
                  innerRef={formProps.register}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  label="Usar a Data do Primeiro Lanamento na Assinatura do Termo de Abertura"
                  name="usarPrimeiraDataLancAssinatura"
                  disabled={isEnabledAberturaEncerramento}
                  id="usarPrimeiraDataLancAssinatura"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <Row className="mb-3">
              <Col>
                <CustomInput
                  type="switch"
                  label="Exibir Numerao da Pgina"
                  name="exibirNroPaginaTermoAE"
                  disabled={isEnabledAberturaEncerramento}
                  id="idexibirNroPaginaTermoAE"
                  innerRef={formProps.register}
                />
                {withTooltip(propToolTip('Exibe a numerao da pgina no relatrio.'), 'exibirNroPaginaTermoAE')}
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  label="Ocultar Data do cabealho"
                  disabled={isEnabledAberturaEncerramento}
                  name="ocultarData"
                  id="ocultarData"
                  innerRef={formProps.register}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="dtTermoAbertura"
                  isPrependLabel={true}
                  formProps={formProps}
                  label="Data do Termo de Abertura"
                  disabled={isEnabledAberturaEncerramento}
                  placeholder="Informe a Data do Termo de Abertura"
                  tooltipProps={propToolTip('Caso este campo esteja vazio a data do termo de abertura ser a data inicial informada.')}
                  type={InputType.DATE}
                />
              </Col>
              <Col>
                <InputGroupInline
                  id="dtTermoEncerramento"
                  isPrependLabel={true}
                  formProps={formProps}
                  label="Data do Termo de Encerramento"
                  disabled={isEnabledAberturaEncerramento}
                  placeholder="Informe a Data do Termo de Encerramento"
                  tooltipProps={propToolTip('Caso este campo esteja vazio a data do termo de encerramento ser a data final informada.')}
                  type={InputType.DATE}
                />
              </Col>
            </Row>
            {isTermoAberturaEncerramento}
            <Row>
              <Col>
                <ResponsavelIS
                  id="representanteAberturaEncerramento"
                  formProps={formProps}
                  label="Representante"
                  disabled={isEnabledAberturaEncerramento}
                  placeholder="Informe o Representante"
                  isPrependLabel={true}
                  inputSearchProps={{ bsbsSize: 'xl', selectProps: { isMulti: false } }}
                />
              </Col>
            </Row>
          </Box>
        </AccordionItem>
      </Accordion>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      />
    </WindowComponent>
  );
};

export default DiarioReportView;
